export default "import {shallow} from 'enzyme';\r\n// tslint:disable-next-line:no-unused-variable\r\nimport * as React from 'react';\r\nimport {TabContent} from '../TabContent';\r\n\r\ndescribe('TabContent', () => {\r\n    describe('<TabContent />', () => {\r\n        it('should render without errors', () => {\r\n            expect(() => {\r\n                shallow(<TabContent />);\r\n            }).not.toThrow();\r\n        });\r\n    });\r\n});\r\n"